body{margin:0;font-family:'Raleway',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;max-width:100vw}ul{list-style:none;padding:0}a{color:#ffffff;text-decoration:none}a:visited{color:#ffffff}a:active{color:#eeeeee}.btn{min-width:130px;min-height:48px;border-radius:4px;font-size:24px;font-weight:600;display:flex;justify-content:center;align-items:center;cursor:pointer;background:#ffffff;color:#080000}.btn:hover{background:#eeeeee}.btn:active{background:#dddddd}.btn-cta{background:#bb0000;color:#ffffff}.btn-cta:hover{background:#aa0000}.btn-cta:active{background:#990000}@media screen and (min-width: 768px){.btn{min-width:100px;min-height:30px;font-size:13px}}h1{font-size:42px}@media screen and (min-width: 992px){h1{font-size:56px}}@media screen and (min-width: 1440px){h1{font-size:72px}}.text-left{text-align:left !important}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-float infinite 3s ease-in-out;animation:App-logo-float infinite 3s ease-in-out}}.App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin)}.App-link{color:#704cb6}@-webkit-keyframes App-logo-float{0%{transform:translateY(0)}50%{transform:translateY(10px)}100%{transform:translateY(0px)}}@keyframes App-logo-float{0%{transform:translateY(0)}50%{transform:translateY(10px)}100%{transform:translateY(0px)}}

footer{background:#000000;padding:30px 25px;display:flex;color:#ffffff}footer .logo{width:88px}footer .logo img{max-width:100%}footer .footer-nav{display:flex;flex-direction:column;font-size:12px;margin-left:25px}footer .footer-nav .title{font-weight:700;margin-bottom:8px}footer .footer-nav .menu-item{cursor:pointer;margin:8px 0}footer .footer-nav .menu-item:hover{color:#eeeeee}

.content-grid{padding-top:80px;display:grid;background:#2a2a2a;grid-template:"item-1 item-1" 1fr "item-1 item-1" 1fr "item-2 item-3" 1fr "item-4 item-5" 1fr "item-6 item-6" 1fr "item-7 item-8" 1fr "item-9 item-10" 1fr "item-11 item-11" 1fr / 1fr 1fr}.content-grid .item-1{grid-area:item-1}.content-grid .item-2{grid-area:item-2}.content-grid .item-3{grid-area:item-3}.content-grid .item-4{grid-area:item-4}.content-grid .item-5{grid-area:item-5}.content-grid .item-6{grid-area:item-6}.content-grid .item-7{grid-area:item-7}.content-grid .item-8{grid-area:item-8}.content-grid .item-9{grid-area:item-9}.content-grid .item-10{grid-area:item-10}.content-grid .item-11{grid-area:item-11}.content-grid .item-12{grid-area:item-12}@media screen and (min-width: 768px){.content-grid{grid-template:"item-1 item-1 item-2 item-3" 1fr "item-1 item-1 item-4 item-5" 1fr "item-6 item-6 item-7 item-8" 1fr "item-9 item-10 item-11 item-11" 1fr / 1fr 1fr 1fr 1fr;grid-gap:25px;gap:25px;padding:130px 50px 50px}}.tile{position:relative;display:flex;color:#ffffff}.tile img{width:100%;display:block}.tile .tile-info{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.tile .tile-info.title-bg{background:rgba(0,0,0,0.5);padding:5px}.tile .tile-info .title,.tile .tile-info .titleMobile{font-size:36px;display:none;white-space:nowrap;text-align:center}@media screen and (min-width: 768px){.tile .tile-info .title,.tile .tile-info .titleMobile{display:block}}.tile .tile-info .titleMobile{font-size:24px;display:block}@media screen and (min-width: 768px){.tile .tile-info .titleMobile{display:none}}.tile .tile-info .btn-cta{font-size:18px;margin-top:15px;min-height:48px}

header{background-color:#000000;display:flex;padding:12px 25px;color:#ffffff;height:57px;align-items:center;position:fixed;top:0;z-index:10;width:calc(100% - 50px)}@media screen and (min-width: 768px){header{padding:10px 50px 0;height:70px;width:calc(100% - 100px)}}header .logo{width:123px;cursor:pointer}header .menu-desktop{width:100%;height:100%;flex-direction:column;justify-content:space-between;display:none}@media screen and (min-width: 768px){header .menu-desktop{display:flex}}header .menu-desktop .login-btns{display:flex;justify-content:flex-end;width:100%}header .menu-desktop .login-btns .btn{margin-left:30px}header .menu-desktop .menu{display:flex;justify-content:space-between;align-items:flex-end;width:100%}header .menu-desktop .menu ul{display:flex;margin:0;width:100%}header .menu-desktop .menu ul li{border-bottom:3px solid #000000;padding:0 15px 3px;font-weight:600;white-space:nowrap;cursor:pointer}header .menu-desktop .menu ul li:hover,header .menu-desktop .menu ul li.active{border-color:#bb0000}header .menu-desktop .menu ul li:active{color:#eeeeee}header .menu-desktop .menu-right{justify-content:flex-end}header .mobileMenuIcon{display:inline-block;margin-left:auto;cursor:pointer}@media screen and (min-width: 768px){header .mobileMenuIcon{display:none}}header .mobileMenuIcon div{width:40px;height:4px;background-color:#d3d3d3;margin:8px 0;transition:.4s}header .mobileMenuIcon.active>div:nth-of-type(1){transform:rotate(-45deg) translate(-9px, 8px)}header .mobileMenuIcon.active>div:nth-of-type(2){opacity:0}header .mobileMenuIcon.active>div:nth-of-type(3){transform:rotate(45deg) translate(-9px, -8px)}header .menu-mobile{height:0;flex-direction:column;position:absolute;top:81px;left:0;width:100vw;background:#000000;z-index:10;transition:.2s}header .menu-mobile>div,header .menu-mobile ul{display:none;opacity:0;transition:.4s}@media screen and (min-width: 768px){header .menu-mobile{display:none !important}}header .mobileMenuIcon.active+.menu-mobile{display:flex;height:calc(100vh - 81px)}header .mobileMenuIcon.active+.menu-mobile>div,header .mobileMenuIcon.active+.menu-mobile ul{display:flex;opacity:1}header .mobile-btns{justify-content:space-between;margin:45px auto;min-width:280px}header .mobileMenu{flex-direction:column;align-items:flex-start;margin:0 25px;font-size:24px}header .mobileMenu li{width:100%;margin-bottom:48px;cursor:pointer}header .mobileMenu li:active{color:#eeeeee}

